export default {
	data() {
		return {
			lbt : [
				
			],
			xingJi:5,
			itemArr:null,
			url:this.url1,
			shopArr:[],
			useUser:this.user,
			shopUser:{}
		}
	},
	onLoad(options) {
		this.itemArr=JSON.parse(options.item);
		this.getshop()
		
		
		
	},
	methods: {
		
		xiajia:function(item,i){
			console.log(item)
			let that=this;
			let usurl=that.url1+"/dinghai/commodity/deleteCommidity";
			uni.showLoading({
				title:'正在下架',
				mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'POST',
						data: {
							id:item.id
						},
				    success: (res) => {
						if(res.data.flag==200){
							uni.hideLoading()
							that.shopArr.splice(i,1)
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
		back : function(){
		   uni.navigateBack({

		   });
		},
		getshop:function(){
			let that=this;
			let usurl=that.url1+"/dinghai/commodity/getbyshop";
			uni.showLoading({
				title:'正在加载',
				mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							shopuid:that.itemArr.id,
							start:1
						},
				    success: (res) => {
						if(res.statusCode==200){
							that.shopArr=res.data.flag;
							for(let i=0;i<that.shopArr.length;i++){
								that.shopArr[i].img1=JSON.parse(that.shopArr[i].img1);
								that.shopArr[i].img2=JSON.parse(that.shopArr[i].img2);
							}
							
							
							uni.request({
										url: that.url1+"/dinghai/user/selectUser",
										header:{
											"Content-Type" : "application/x-www-form-urlencoded"
										},
										method:'GET',
										data: {
											uid:that.itemArr.uid
										},
								    success: (res1) => {
										that.shopUser=res1.data.flag
								    },
									fail : function(err){
										console.log(err)
										uni.showToast({
											title:"您的网络打了个盹",
											icon:"none"
										})
									}
								});
							
							
							uni.showToast({
								title:"加载成功",
								icon:"none"
							})
						}else{
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
		goShopping:function(a,b){
			uni.navigateTo({
				url:"../goShopping/goShopping?goShopping="+JSON.stringify(a)+'&shop='+JSON.stringify(b)
			})
		}
	}
}
